@use "../setting/color" as *;
//=================================
// base color asset
//=================================
//---------------------------------
// change variable
// $b_blue, $b_darkblue, $b_red, $b_pink, $b_orange,$b_green $b_silver
$base: #0043B0;
//---------------------------------
// change variable
$theme: $t_light;
//---------------------------------


// color assets
//---------------------------------
$basea: rgba($base, 0.77);
$baseabg: rgba($base, 0.16);
$base_hover: darken($base, 20%);
$base_font: $theme;
$base_font_reverse: invert($base_font);
$base_reverse: complement($base);
$base_reverse_hover: darken(complement($base), 20%);
//---------------------------------
$base_bg: $theme;
$base_bg_even: darken($theme, 1%);
$accent_bg: #2C343E;
$base_inner_bg: #F6F8F8;
$base_bg_color: invert(lighten($theme, 10%));
//---------------------------------