@use "../global" as g;

// ======================================================================
// p-service
// ======================================================================

.p-privacy {
  .l-main {
    .l-contents {
      .l-sec {
        margin: 0;

        &.l-privacy {
          padding: 0 0 calc(100vw * 200 / 1920);
          padding-inline: calc(100vw * 200 / 1920);

          @include g.sp {
            padding: 0 0 calc(100vw * 50 / 375);
            padding-inline: calc(100vw * 16 / 375);
          }

          /*  */
          .lead {
            font-size: calc(100vw * 20 / 1920);
            font-weight: 500;
            margin: calc(100vw * 50 / 1920);

            @include g.sp {
              font-size: calc(100vw * 16 / 375);
              margin: calc(100vw * 24 / 375);
            }
          }

          /*  */
          .txt {
            font-size: calc(100vw * 20 / 1920);
            font-weight: 500;
            margin: calc(100vw * 30 / 1920);

            @include g.sp {
              font-size: calc(100vw * 16 / 375);
              margin: calc(100vw * 20 / 375);
            }
          }

          /*  */
          .pri_ttl {
            font-size: calc(100vw * 32 / 1920);
            font-weight: 700;
            margin: calc(100vw * 50 / 1920);

            @include g.sp {
              font-size: calc(100vw * 22 / 375);
              margin: calc(100vw * 30 / 375);
            }
          }
        }
      }
    }
  }
}