@use "../global" as g;

/* ===================================== */
/* 1.  (l-breadcrumbs, breadcrumb) */
/* ===================================== */
.l-breadcrumbs {
  /*  */
  padding: 10px 0;
  margin: 10px 0;
}

.breadcrumb {
  /*  */
  font-size:calc(100vw * 24 / 1920);
  color: #666;
  /*  */
  padding: 0 15px;
  /*  */
  @include g.sp {
    font-size: calc(100vw * 14 / 375);
  }
}

/* ===================================== */
/* 2.  */
/* ===================================== */

/*  */
.breadcrumb span {
  white-space: nowrap;
  /*  */
}

/* a */
.breadcrumb a {
  color: #337ab7;
  /*  */
  text-decoration: none;
  /*  */
  transition: color 0.2s ease;
}

.breadcrumb a:hover {
  color: #1a568c;
  /*  */
  text-decoration: underline;
}

/*  */
.breadcrumb .separator {
  padding: 0 5px;
  /*  */
  color: #999;
  /*  */
}

/* ===================================== */
/* 3.  () */
/* ===================================== */

/* span */
.breadcrumb span:last-child {
  font-weight: bold;
  /*  */
  color: #333;
  /*  */
}
