@use "../global" as g;

/*
 *  ()
 */
$main-color: #333; // 
$sp-breakpoint: 767px; // SPPC

/*
 *  (.openbtn4)
 */
.openbtn4 {
    /* SP */
    display: none;

    @include g.sp {
        display: block;
        position: fixed;
        /*  */
        top: calc(100vw * 0 / 375);
        /*  */
        right: calc(100vw * 10 / 375);
        /*  */
        z-index: 9999;
        cursor: pointer;
        width: calc(100vw * 40 / 375);
        height: calc(100vw * 40 / 375);
    }
}

.openbtn4 span {
    display: block;
    height: 3px;
    background: $main-color;
    position: absolute;
    width: 60%;
    left: 20%;
    transition: all .4s;
    border-radius: 4px;
}

.openbtn4 span:nth-child(1) {
    top: 30%;
}

.openbtn4 span:nth-child(2) {
    top: 50%;
}

.openbtn4 span:nth-child(3) {
    top: 70%;
}

/*  (active) */
.openbtn4.active span:nth-child(1) {
    top: 50%;
    transform: rotate(45deg);
}

.openbtn4.active span:nth-child(2) {
    opacity: 0;
}

.openbtn4.active span:nth-child(3) {
    top: 50%;
    transform: rotate(-45deg);
}


/*
 * SP (.l-spnav)
 */
.l-spnav {
    /* SP */
    display: none;

    @media screen and (max-width: $sp-breakpoint) {
        display: block;
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100vh;
        /*  */
        background: #2c2c2c;
        /*  */
        z-index: 9990;
        /*  */

        /*  */
        transform: translateX(100%);
        transition: transform 0.6s ease;
        overflow-y: auto;
        /*  */
        padding-top: 80px;
        /*  */
    }

    /*  (active) */
    &.active {
        transform: translateX(0);
    }

    .lists {
        list-style: none;
        padding: 0;
        margin: 0;
    }

    .items {
        text-align: center;
        // border-bottom: 1px solid #eee;

        // &:first-child {
        //     border-top: 1px solid #eee;
        // }

        &:last-child {
            background: #fff;
            padding: 15px;
            margin: 10px 0 0;
        }
    }

    .link {
        display: block;
        padding: 15px 20px;
        text-decoration: none;
        color: #fff;

        .en {
            font-size: 2.1rem;
            font-weight: bold;
        }

        .ja {
            font-size: 1.4rem;
            margin-top: 3px;
        }

        &.cta_btn {
            background: #fff;
            /* CTA */
            color: #2c2c2c;
            font-size: 2.1rem;
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            border: solid 3px #2c2c2c;

            &:active {
                background: #2c2c2c;
                color: #fff;
            }


            i {
                margin-right: 5px;
            }
        }
    }
}

/*
 * PC (SP)
 */
.l-nav.__pc {
    @media screen and (max-width: $sp-breakpoint) {
        display: none;
    }
}