@use "../global" as g;

// ======================================================================
// p-service
// ======================================================================

.p-sitemap {
  .l-main {
    .l-contents {
      .l-sec {
        margin: 0;

        &.l-sitemap {
          padding: 0 0 calc(100vw * 200 / 1920);
          padding-inline: calc(100vw * 200 / 1920);

          @include g.sp {
            padding: 0 0 calc(100vw * 50 / 375);
            padding-inline: calc(100vw * 16 / 375);
          }

          /*  */
          .u_head_ttl_02_center {
            margin-bottom: calc(100vw * 80 / 1920);

            @include g.sp {
              margin-bottom: calc(100vw * 40 / 375);
            }
          }

          /*  */
          .sitemap_lists {
            list-style: none;
            padding: 0;
            margin: 0;
          }

          /*  */
          .sitemap_item {
            margin-bottom: calc(100vw * 40 / 1920);

            @include g.sp {
              margin-bottom: calc(100vw * 24 / 375);
            }

            a {
              font-size: calc(100vw * 24 / 1920);
              font-weight: 600;
              color: #333;
              text-decoration: none;
              transition: opacity 0.3s;

              &:hover {
                opacity: 0.7;
              }

              @include g.sp {
                font-size: calc(100vw * 18 / 375);
              }
            }

            /*  */
            &.has-children {
              margin-bottom: calc(100vw * 50 / 1920);

              .child_lists {
                list-style: none;
                padding: calc(100vw * 12 / 1920) 0 0;
                padding-left: calc(100vw * 25 / 1920);

                @include g.sp {
                  padding-left: calc(100vw * 16 / 375);
                }

                li {
                  margin-bottom: calc(100vw * 20 / 1920);

                  @include g.sp {
                    margin-bottom: calc(100vw * 14 / 375);
                  }

                  a {
                    font-size: calc(100vw * 20 / 1920);
                    font-weight: 500;
                    color: #555;

                    @include g.sp {
                      font-size: calc(100vw * 16 / 375);
                    }
                  }
                }
              }
            }
          }

        }
      }
    }
  }
}